.header__menu-burger {
  cursor: pointer;
  width: 55px;
  height: 65px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: none;
  position: absolute;
  top: calc(50% - 22px);
  left: 0;
  right: 25px;
}

.header__menu-burger-line {
  width: 30px;
  height: 3px;
  background-color: #fff;
  transition: all .2s;
  display: inline-block;
  position: relative;
}

.header__menu-burger-line:before, .header__menu-burger-line:after {
  content: "";
  width: 30px;
  height: 3px;
  background-color: #fff;
  transition: all .2s;
  position: absolute;
  left: 0;
}

.header__menu-burger-line:after {
  top: -9px;
}

.header__menu-burger-line:before {
  top: 9px;
}

@media (max-width: 920px) {
  .header__menu-burger {
    display: flex;
  }
}

.header__burger-checkbox {
  display: none;
}

.header__burger-checkbox:checked + .header__menu-burger .header__menu-burger-line {
  top: 0;
  transform: rotate(-45deg);
}

.header__burger-checkbox:checked + .header__menu-burger .header__menu-burger-line:after {
  top: 0;
  transform: rotate(-90deg);
}

.header__burger-checkbox:checked + .header__menu-burger .header__menu-burger-line:before {
  display: none;
}

.header__burger-checkbox:checked ~ .menu-nav, .header__burger-checkbox:checked ~ .header__theme-button-box {
  transition: all .2s;
